/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .qodef-cart-label,
        .qodef-cart-number {
            font-family: $default-heading-font;
            font-weight: 700;
            font-size: 15px;
        }

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 22px;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }

                path {
                    stroke-width: 0.5px;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 301px;
    background-color: $default-heading-color;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include qodefTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 35px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 12px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                color: #fff;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 51px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        vertical-align: top;
        padding-left: 14px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #fff;
            letter-spacing: 0.005em;
            line-height: 1.05em;

            a {
                &:hover {
                    color: rgba(#fff, 0.8);
                }
            }
        }

        .remove {
            position: absolute;
            top: -1px;
            right: -8px;
            font-size: 18px;
	        line-height: 1;
            color: #fff;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: inline-block;
            margin: 3px 0 0;
            color: #fff;
            font-size: 14px;
        }

        .amount {
            display: inline-block;
            margin: 3px 0 0;
            color: #fff;
            font-size: 14px;
        }
    }

    .qodef-cart-bottom {
        padding: 13px 0 0;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 18px;
            font-weight: 700;
            font-family: $default-heading-font;
            font-size: 17px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: #fff;
                line-height: 20px;
            }

            .qodef-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .qodef-view-cart {
            @include qodefRelativeHolderLayout();
            height: 51px;
            line-height: 50px;
            padding: 0 26px;
            font-size: 14px;
            font-weight: 700;
            font-family: $default-heading-font;
            color: #fff;
            border: 2px solid #fff;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: $default-heading-color;
                background-color: #fff;
                border-color: transparent;
            }
        }

        .qodef-checkout {
            @include qodefRelativeHolderLayout();
            height: 51px;
            line-height: 50px;
            padding: 0 26px;
            font-size: 14px;
            margin: 0 0 12px;
            font-weight: 700;
            font-family: $default-heading-font;
            color: $default-heading-color;
            background-color: #fff;
            border: 2px solid transparent;
            text-align: center;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: transparent;
                border-color: #fff;
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */