/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonMediumParams();
        @include qodefButtonSolidColor();
        color:#000;
        cursor: pointer;
        z-index: 3;

        &:hover {
           opacity: .9;
        }
    }
    
    a.button.added {
        display: none;
    }

    .single_add_to_cart_button {
        position: relative;
        overflow: hidden;
        vertical-align: middle;
        @include qodefTransition(color .1s .03s);
        

        &:before,
        &:after {
            content: '';
            @include qodefAbsoluteHolderLayout();
            width: 101%;
            @include qodefTransitionTransform(.3s cubic-bezier(0.215, 0.61, 0.355, 1));
            z-index: -1;
        }

        &:before {
            @include qodefTransform(scaleX(1));
            @include qodefTransformOrigin(100% 50%);
        }

        &:after {
            @include qodefTransform(scaleX(0));
            @include qodefTransformOrigin(0% 50%);
            background-color: $first-main-color;
        }

        &:hover {
            &:before {
                @include qodefTransform(scaleX(0));
            }
    
            &:after {
                @include qodefTransform(scaleX(1));
            }
        }
    }

    @include keyframes(qodefCustomWooBtnHover) {
        0% {
            padding: 7px 46px 7px 26px;
            color: $first-main-color;
        }

        100% {
            padding: 7px 36px 7px 36px;
            color: #fff;
        }
    }

    .single_add_to_cart_button.button.alt {
        padding: 7px 36px;
        font-size:13px;
        color: $first-main-color;
        
        &:hover {
            @include qodefAnimation(qodefCustomWooBtnHover .65s cubic-bezier(0.215, 0.61, 0.355, 1) .03s forwards);
        }
    }

}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */