/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .qodef-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: $first-main-color;


                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 10px;
                    width: 10px;
                    border-radius: 10px;
                    background-color: $default-heading-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include qodefTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: #acacac;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 14px 0 0;
            
            .button {
                float: right;
                line-height: 24px;
                padding: 0;
                color: $default-heading-color;
                background-color: transparent;
                border: none;

                &:after {
                    position: absolute;
                    display: block;
                    content: " ";
                    left: 0;
                    bottom: 3px;
                    width: 0;
                    border-bottom: 2px solid #000;
                    @include qodefTransition(width .23s);
                }

                &:hover {
                    border-color: transparent;
                    &:after {
                        width: 100%;
                        @include qodefTransition(width .23s);
                    }
                }

                @include laptop-landscape-mac {
                    float: left;
                    margin: 0 0 10px;
                }
            }
            
            .price_label {
                float: left;
                font-size: 17px;
                font-family: $default-heading-font;
                font-weight: 700;
                color: $first-main-color;

                @include ipad-landscape {
                    padding: 11px 0 11px 15px;
                }
            }
        }

        .qodef-widget-title {
            margin: 0 0 21px;
        }
    }
    
    &.widget_product_categories {

        .qodef-widget-title {
            margin: 0 0 11px;
        }
        
        ul {
            
            li {
                margin: 2px 0 10px;

                a {
                    color:#464646;

                    &:hover {
                        border-bottom:2px solid black;
                    }
                }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .qodef-widget-title {
            margin: 0 0 24px;
        }
        
        ul {
            
            li {
                min-height: 84px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 99px;
                        line-height: 20px;
                        box-sizing: border-box;

                        &.product-title {
                            font-size: 17px;
                            line-height: 1.23em;
                            font-weight: 700;
                            color: $default-heading-color;
                            font-family: $default-heading-font;
                            text-transform: uppercase;
                        }
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 99px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                    font-size: 14px;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {

                .product-title {
                    font-family: $default-heading-font;
                    font-weight: 700;
                    color: #1d1d1d;
                    font-size: 17px;
                    text-transform: uppercase;
                    margin: 17px 0 0;
                }
                
                del,
                ins,
                > .amount {
                    color: $default-text-color;
                    font-size: 14px;
                    margin-top: 3px;
                }

                > .amount {
                    display: block;
                }

                del {
                    display: inline-block;
                    + ins {
                        display: inline-block;
                        margin-left: 0;
                    }
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;
                
                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
            
            > div {
                @include qodefRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: inherit;
                font-size: 13px;
    
                @include qodefPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 14px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */