/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
			width: 47.8%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;


		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }

		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 10px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 0 0 11px;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();
			font-size: 23px;

	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
			margin: 52px 0 3px;

			.woocommerce-review-link {
				font-size: 14px;
				color: $default-text-color;

			}

	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 16px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 48px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
			margin: 48px 0 0;
	    }
	
	    .qodef-quantity-buttons {
			margin: 0 11px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 58px 0 0;

		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				border-bottom: 2px solid #e0e0e0;
				margin-right: -4px;
				@include qodefTransition(all .3s ease);

				&:hover,
				&.active {
					border-bottom: 2px solid $first-main-color;
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 9px 25px;
					font-size: 16px;
					text-transform: uppercase;
					font-weight: 700;

					&:hover {
						color: $default-heading-color;
					}
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 43px 0 20px;
				line-height: 27px;
			}
		}
		
		table {
			margin: 40px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-text-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-weight: 400;
			}
			
			td {
				width: 99%;
			}
		}

		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 40px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;

					&.comment-form-author,
					&.comment-form-email {
						> label {
							font-family: $default-text-font;
							text-transform: uppercase;
							font-weight: 700;
							font-size: 14px;
						}
					}
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 40px;
			font-size: 23px;
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */